import { createSlice } from "@reduxjs/toolkit";



// Find an empty home cell for the given color.
function findHome(state, color) {
  const homes = {
    green: ["g00", "g01", "g02", "g03"],
    yellow: ["y00", "y01", "y02", "y03"],
    blue: ["b00", "b01", "b02", "b03"],
    red: ["r00", "r01", "r02", "r03"],
  }
  return homes[color].find(cell => !state.find(piece => piece.cell === cell))
}

const gameSlice = createSlice({
  name: "game",
  initialState: [
    {id: "g1", color: "green", cell: "g00"},
    {id: "g2", color: "green", cell: "g01"},
    {id: "g3", color: "green", cell: "g02"},
    {id: "g4", color: "green", cell: "g03"},
    {id: "y1", color: "yellow", cell: "y00"},
    {id: "y2", color: "yellow", cell: "y01"},
    {id: "y3", color: "yellow", cell: "y02"},
    {id: "y4", color: "yellow", cell: "y03"},
    {id: "b1", color: "blue", cell: "b00"},
    {id: "b2", color: "blue", cell: "b01"},
    {id: "b3", color: "blue", cell: "b02"},
    {id: "b4", color: "blue", cell: "b03"},
    {id: "r1", color: "red", cell: "r00"},
    {id: "r2", color: "red", cell: "r01"},
    {id: "r3", color: "red", cell: "r02"},
    {id: "r4", color: "red", cell: "r03"},
  ],
  reducers: {
    movePiece(state, action) {
      const { payload } = action;
      console.log(payload)
      const occupied = state.find(piece => piece.cell === payload.cell)
      if (occupied) {
        occupied.cell = findHome(state, occupied.color)
      }
      state.find(piece => piece.id === payload.piece).cell = payload.cell
    },
  },
})

export const { movePiece } = gameSlice.actions

export default gameSlice.reducer
