const idMap = {
  color: {
    g: "green",
    y: "yellow",
    r: "red",
    b: "blue",
  },
  type: {
    "0": "start",
    "1": "common",
    "2": "home",
  },
}

function cellInfo(id) {
  return {
    id: id,
    color: idMap.color[id[0]],
    type: idMap.type[id[1]],
    safe: id[1] === "1" && id[2] === "5",
    coords: coords[id],
  }
}

function Piece({ movePiece, cell, color, id, scale = 1.0 }) {
  const [{isDragging}, drag] = useDrag({
    item: { type: "piece", color: color, id: id},
    collect: monitor => ({
      isDragging: !!monitor.isDragging(),
    }),
    end: (item, monitor) => {
      const result = monitor.getDropResult();
      console.log("DropResult", result)
      console.log(movePiece)
      if (result) {
        movePiece({piece: id, cell: result.cell});
      }
    },
  })

  const [{isOver, canDrop}, drop] = useDrop({
      accept: "piece",
      drop: () => ({cell: cell.id}),
      collect: mon => ({
        isOver: !!mon.isOver(),
        canDrop: !!mon.canDrop(),
      }),
      canDrop: (item, mon) => {
        return true
      },
  })

  const classes = classnames({
    Piece: true,
    [color]: true,
    dragging: isDragging,
		over: isOver && canDrop,
  })

  const style = {
    top: (cell.coords.cx - 10) * scale,
    left: (cell.coords.cy - 10) * scale,
    height: 20 * scale,
    width: 20 * scale,
    padding: 2 * scale, // to match border of cell
  }

  return (
    <div ref={drag} className={classes} style={style}>
      <div ref={drop} style={{height: '100%', width: '100%'}} />
    </div>
  )
}

const ConnectedPiece = connect(null, { movePiece })(Piece)


function Cell({ id, color, type, safe, coords, scale = 1.0}) {
  const [{isOver, canDrop}, drop] = useDrop({
      accept: "piece",
      drop: () => ({cell: id}),
      collect: mon => ({
        isOver: !!mon.isOver(),
        canDrop: !!mon.canDrop(),
      }),
      canDrop: (item, mon) => {
        return (type === "common" || color === item.color)
      },
  })

  const classes = classnames({
    "Cell": true,
    [color]: type !== "common",
    "safe": safe,
		"over": isOver && canDrop,
  })
  const style = {
    top: (coords.cx - 10) * scale,
    left: (coords.cy - 10) * scale,
    height: 20 * scale,
    width: 20 * scale,
    borderWidth: 2 * scale,
  }
  return (
    <div ref={drop} className={classes} style={style} />
  )
}

function Board({ gameState }) {
  const {innerHeight, innerWidth} = useWindowSize();
  const size = Math.min(innerHeight, innerWidth) - 40;
  const scale = size / 410;

  return (
    <DndProvider backend={isTouch ? TouchBackend : Html5Backend}>
      <div className="Board" style={{width: size, height: size}}>
        {
          Object.keys(coords).map(id => {
            const cell = cellInfo(id);
            return <Cell key={id} scale={scale} {...cell} />
          })
        }
				{
					gameState.map(({ id, color, cell }) => (
            <ConnectedPiece key={id} id={id} color={color} scale={scale} cell={cellInfo(cell)} />
					))
				}
      </div>
    </DndProvider>
  )
}

export default connect(state => ({gameState: state}))(Board);
